.wholeDiv {
    padding-left: 25%;
    padding-right: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  
  .pageTitle {
    padding-left: 22%;
    padding-right: 20%;
    color: #4d1f00;
    font-size: 15px;
    font-weight: bolder;
    font-family: 'Balsamiq Sans', cursive;
  }
  
  .titleDiv {
    color: #4d1f00;
    font-size: 15px;
    font-weight: bolder;
    font-family: 'Sansita Swashed', cursive;
    background-color: #66cc66;
    border-radius: 30%;
    padding-left: 15px;
    padding-right: 15px;
    margin: 20px;
    box-shadow: 5px 5px 10px #b3e6b3;
    border: 5px solid #39ac39;
  }
  
  .componentsList {
    list-style: none;
    display: grid;
    grid-template-columns: 1fr;
    column-gap: 10px;
    row-gap: 10px;
    grid-auto-rows: 1fr;
  }
  
  .formDiv {
    border-radius: 15px;
    padding-left: 15px;
    padding-right: 15px;
    margin: 20px;
    border: 5px dashed #b3e6ff;
  }
  
  .formTitle {
    color: #4d1f00;
    font-size: 20px;
    font-weight: bolder;
    font-family: 'Sansita Swashed', cursive;
  }
  
  @media (min-width: 700px) {
    .componentsList {
      grid-template-columns: 1fr;
    }
  }
  
  @media (min-width: 900px) {
    .componentsList {
      grid-template-columns: 1fr 1fr;
    }
  }